.sidebar {
  background: white;
  width: 0px;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  z-index: 2000;
  transition: 0.1s;
  overflow: hidden;
}

.sidebar.open {
  width: 310px;
}

.background {
  background: #41414187;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
}

.nav {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100px;
  padding: 100px 80px;
}

.nav a {
  color: black;
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: 500;
  text-decoration: none;
}

.nav a:hover {
}

.nav a.active {
  color: #0066cc;
  text-decoration: underline;
}
.close {
  position: absolute;
  top: 20px;
  right: 20px;
}

.close img {
}
