.radio {
  margin-right: 10px;
  position: relative;
  opacity: 1;
  font: inherit;
  width: 1.15em;
  height: 1.15em;
  border: 0.1em solid rgb(155, 0, 147);
  border-radius: 50%;
  appearance: none;
}

.radio::before {
}

.radio:checked::before {
  position: absolute;
  content: "";
  top: 2.5px;
  left: 2.4px;
  width: 10.5px;
  height: 10.5px;
  background: rgb(230, 7, 238);
  border-radius: 50%;
}

.label {
  cursor: pointer;
}

.options {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
